* {
  box-sizing: border-box;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: #000;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  overflow: hidden;
}

#root {
  overflow: auto;
}

.main {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

canvas {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  z-index: 0;
  overflow: hidden;
}

.planet-name {
  position: absolute;
  top: 200px;
  left: 50%;
  font-family: "Poiret One", cursive;
  font-weight: 800;
  font-size: 8em;
  transform: translate3d(-50%, 0, 0);
  margin: 0;
  color: #fff;
  line-height: 0.9em;
  letter-spacing: -5px;
  text-align: center;
}
